export default {
  state: {
    loading: false, //loading
    notify: {
      type: "text",
      position: "top",
      text: ""
    } //
  },
  mutations: {
    loadingCtrl(state, bool) {
      state.loading = bool;
    },
    notify(state, notifyOpts) {
      state.notify = Object.assign({},  state.notify, notifyOpts);
    }
  },
  actions: {},
  getters: {}
}



